@import url('https://fonts.googleapis.com/css2?family=Shadows+Into+Light&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Jost:wght@300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@600&display=swap');
*{
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}
body {
  background-color: #eeeeee;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.nav-class
{
  font-size: 14px;
  display:flex;
  justify-content:flex-end;
  align-items:center;
  padding:5px;
  border-radius: 5px;
  padding-top:28px;
  /* background-color:rgb(202,201,201); */
}
.name-class
{
  font-size:30px;
  margin-right: auto;
  color:rgb(149,135,60);
  font-family: 'Shadows Into Light', cursive;
  margin-left: 20px;
  

}
.ul-class
{
  list-style: none;
  display:flex;
  justify-content: space-between;
 
}

.intro-text
{
  text-align: center;
}

.intro-class
{
  margin-top:120px;
  
  text-align: center;
  font-size: 18px;
  display:flex;
  justify-content:center;
  flex-direction: column;
  align-items: center;
  font-family: 'Jost', sans-serif;
  margin-bottom: 100px;
}
.smile-emoji{
  color:rgb(247,199,77);
  margin-left: 15px;
}
.intro-photo
{
  height: 200px;
  width:200px;
  border-radius: 50%;
  background-size: cover;
  border:3px solid rgb(236, 228, 228);
  padding:10px;
  background-image: url(./photos/2.jpg);
  margin-bottom: 30px;
}
.intro-title
{
  font-size: 27px;
  display:flex;
  align-items:center;
  text-align: center;
  justify-content: center;
}
.resume-class
{
  display: flex;
  justify-content:center;
}
.skill-class{
  padding:25px;
}
.topic
{
  font-family: 'Dancing Script', cursive;
  font-size:32px;
  color:rgb(206, 181, 37);
  margin-left:15px;
}
.cont-o
{
  margin-top: 50px;
  
  max-width: 1400px;
  margin:0 auto;
  padding: 15px;

}
.row-1
{
  
  display:flex;
  justify-content: space-evenly;
  /* background-color: rgba(231, 231, 231, 0.822); */
  background-color:rgb(252, 252, 252) ;
  border-radius: 8px;
  font-family: 'Jost', sans-serif;
  box-shadow: 10px 10px 5px rgb(218, 213, 213),
  -10px -10px 5px rgb(212, 211, 211);
    
              

}
.col-item
{
   /* padding:20px;
   margin-right:50px;
   */
   margin-top:10px;
   margin-right:15px;
   padding:15px;
   display:flex;
   flex-direction: column;
   align-items: center;
}
.s-icon
{

  color:rgb(216, 209, 209);

}
.hobby-class{
  padding:25px;
}
.joke
{
  text-align: center;
  font-size: 19px;
  font-weight: bold;
  font-family: 'Jost', sans-serif;
  padding:5px;
  margin-bottom: 100px;
  margin-top: 100px;
}
.contact-class
{
  display:flex;
  flex-direction: column;
  align-items: center;
 position: relative;
}
.contact-ul-class
{
  list-style: none;
  display: flex;
  align-items: center;
  justify-content:space-between;
  
}
.contact-ul-class a
{
  text-decoration: none;
  margin-right: 40px;

}
.contact-ul-class li
{
  color:black;
  
  
  margin-bottom: 25px;
 
}
.fab,.fas
{
  color:#241f1f;
  transition:all 0.5s linear  0s;
}
.wi:hover,.wi:focus
{
 color:rgb(0,190,164);
transform:scale(1.1,1.1);

}
.gi:hover
{
 color:rgb(162, 37, 179);
transform:scale(1.1,1.1);

}
.ti:hover
{
 color:rgb(40,168,233);
transform:scale(1.1,1.1);

}
.mi:hover
{
 color:rgb(224, 166, 41);
transform:scale(1.1,1.1);

}
.li:hover
{
  color:rgb(10,102,194);
  transform:scale(1.1,1.1);

}
.fa-smile-wink
{
color:rgb(225,178,54);
}
.a-cont
{
   max-width: 800px;
   margin: 0 auto;
   padding:25px;
}

.item
{
 display:flex;
 margin-top: 15px;
 justify-content:center;
 margin-top: 10px;
 font-size: 17px;
}
.item p
{
  margin-left: 25px;
  margin-right: 25px;

}

.p-cont
{

  max-width: 1400px;
 margin:0 auto;
 padding:15px;
 margin-bottom: 120px ;
}
.row-p
{

  
  display: flex;
  justify-content:space-evenly;
  align-items:center;
  font-family: 'Jost', sans-serif;
  

}
.p-item{
  background-color:white;
  text-align: center;
  margin-top:25px;
  margin-right: 15px;
  margin-bottom:25px;
  height: 450px;
  border-radius: 10px;
}
.p-img
{
  background-color: red;
  height: 200px;
  margin:8px;
  margin-top: 20px;
  /* background-image: url("./photos/1.png"); */
  background-size: cover;
  background-position:center;
  border:1px solid grey;
  border-radius: 8px;
  transition:all 1.0s ease-in-out 0.3s;
  margin-bottom: 35px;
}
.p-img:hover
{
  transform:scale(2,2);
  
}
.link
{
  font-size:25px;
  display:flex;
  justify-content:space-evenly;
  align-items:center;
  margin-top:20px;
  
}
.p-title{
  font-size:18px;
  margin-top:10px;
  font-weight: bold;
}
.wlink
{
  transition:all 0.7s linear  0s;

}
.wlink:hover
{
  color:rgb(218,31,38);

}
#clink,#plink{
  text-decoration: none;
    margin-right:25px;
    color:rgb(115,179,154);
}
.fa-arrow-circle-up{
  margin:5px;
  position: absolute;
  bottom:0px;
  right: 0px;
}
@media(max-width:700px)
{
  #clink{
      margin-right:5px;
  }
  .name-class
  {
    font-size: 21px;
   

  }
  body
  {
    font-size: 14px;

  }
  .intro-title
{
  font-size: 22px;
  
}

}

@media (max-width: 400px)
{

  .row-1{
    padding: 10px;
  }
.col-item
{
 
   font-size: 12px;
   width:45px;
}
.joke
{
  font-size: 14px;

}
.p-item{
  background-color:white;
  text-align: center;
  height: 450px;
  border-radius: 10px;
  margin:25 auto;
  margin-left: 20px;
}
.intro-title
{
  font-size: 19px;
}
.p-img
{
  background-color: red;
  height: 200px;
  margin:8px;
  margin-top: 20px;
  /* background-image: url("./photos/1.png"); */
  background-size: cover;
  background-position:center;
  border:1px solid grey;
  border-radius: 8px;
  transition:all 0.6s ease-in-out 0.3s;
  margin-bottom: 35px;
}
.p-img:hover
{
  transform:none;
  
}
}

@media (max-width: 450px) and (min-width:400px) 
{
 
 .row-1{
  padding: 15px;
}
.col-item
{
 
   font-size: 15px;
   width:45px;
}
.p-item{
  background-color:white;
  text-align: center;
  height: 450px;
  border-radius: 10px;
  margin:25px auto;
  margin-left: 10px;
}
.p-img:hover
{
  transform:none;
  
}
}
@media(max-width:300px)
{


  
  .name-class
{
  font-size:19px;
  margin-left: 10px;
  font-size: 14px;

}

}
.nav-l:hover{
  color:pink;
}
.active
{
  color:red;

}